img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.title {
  margin: 0;
  color: $textСolor-main;
}

.button {
  font-family: $fontFamily;
  font-weight: 700;
  text-transform: uppercase;
  color: $textСolor-secondary;
}
